/* Error Table Container */
.error-table-container {
  border-radius: 10px;
  padding: 10px;
}

/* Custom Table */
.custom-error-table {
  border-collapse: collapse;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.15);
  font-size: 14px;
  width: 100%;
}

/* Table Header */
.custom-error-table thead {
  background: #d32f2f;
  color: white;
  font-size: 14px;
  text-align: center;
}

/* Alternating Row Colors */
.custom-error-table tbody tr:nth-child(odd) {
  background: #fff;
}

.custom-error-table tbody tr:nth-child(even) {
  background: #f4f4f4;
}

/* Row Number */
.row-number {
  font-weight: bold;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
}

/* Column Name and Error Description */
.column-name,
.error-description {
  padding: 8px;
  word-wrap: break-word;
  white-space: pre-line;
}

/* No Errors Found Row */
.no-errors {
  color: #28a745;
  font-weight: bold;
}

/* Custom Alert */
.custom-alert {
  border-radius: 6px;
  font-size: 13px;
  background: rgba(211, 47, 47, 0.1);
  padding: 8px;
  color: #d32f2f;
}
